.feedback {
  padding-left: 375px;
  padding-right: 375px;
  background: $white;
  min-height: 455px;

  @media (max-width: $tablet-width) {
    padding: 0;
  }

  @media (max-width: $mobile-width) {
    background-color: $light;
  }
}

.feedback__container {
  background: url("../img/sunset-desktop.jpg") no-repeat; // top left;
  //background-size: 844px 455px;
  //background-position: 100% 50%; //50% 62%;
  min-height: 455px;
  padding-top: 70px;

  @media (max-width: $tablet-width) {
    padding: 70px 55px 74.59px 55px;
    background: url("../img/sunset-tablet.jpg") no-repeat; // top left;
  }

  @media (max-width: $mobile-width) {
    background: none;
    padding: 50px 16px 50px 16px;
  }
}

.feedback__form {
  //margin-top: 70px;
  margin-left: 514px;

  @media (max-width: $tablet-width) {
    margin-left: auto;
  }
}
